
import {FormAction} from "redux-form";
import { InferActionType, BaseThuncType} from "./store";
import {chatApi, ChatMessageType, StatusType} from "../api/chat_api";
import { Dispatch } from "redux";
import {v1} from 'uuid'


let initialState = {
    messages:[] as MessageType[],
    status: 'panding' as StatusType
};

export type InitialStateType = typeof initialState;
type MessageType = ChatMessageType & {id: string};
type ActionsType= InferActionType<typeof actionsReducer>;
type thuncType = BaseThuncType<ActionsType| FormAction>

const chatReducer =  (state = initialState, action:ActionsType):InitialStateType => {
    switch (action.type) {
        case    "SET_MESSAGES":
            return {
                ...state,
                messages: [...state.messages, ...action.data.messages.map(m => ({...m, id: v1()} ))].filter((m, index, array) => index >= array.length - 50)
            };
        case    "SET_STATUS_WS":
            return {
                ...state,
                status: action.data.status
            };
        default:
            return state;
    }
};

export const actionsReducer={
    setMessages: (messages:ChatMessageType[]) => ({
        type: "SET_MESSAGES", data: {messages}
    } as const),
    setStatus: (status:StatusType) => ({
        type: "SET_STATUS_WS", data: {status}
    } as const),
};

let _newMessageHandl:((messages:ChatMessageType[]) => void) | null = null;
const newMessageHandler = (dispatch:Dispatch) =>{
   if (_newMessageHandl === null) {
       _newMessageHandl = (messages) => {
           dispatch(actionsReducer.setMessages(messages));
       }
   }
return _newMessageHandl
};
let _statusChangeHandl:((status:StatusType) => void) | null = null;
const statusChangeHandler = (dispatch:Dispatch) =>{
    if (_statusChangeHandl === null) {
        _statusChangeHandl = (status) => {
            dispatch(actionsReducer.setStatus(status));
        }
    }
    return _statusChangeHandl
};



//thunk
export  const startMessagesListening = ():thuncType => async (dispatch) => {
    chatApi.startWsChannel();
    chatApi.subscribe('message', newMessageHandler(dispatch));
    chatApi.subscribe('statusChanged', statusChangeHandler(dispatch));
};
export  const stopMessagesListening = ():thuncType => async (dispatch) => {
    chatApi.unsubscribe('message',newMessageHandler(dispatch));
    chatApi.unsubscribe('statusChanged',statusChangeHandler(dispatch));
    chatApi.stopWsChannel();
};
export  const sendMessages = (messages:string):thuncType => async (dispatch) => {
    chatApi.sendMessages(messages)
};
export  const getStatusWs = (status:StatusType):thuncType => async (dispatch) => {
    dispatch(actionsReducer.setStatus(status)) ;
};


export default chatReducer;