export type StatusType ='panding'|'ready'|'error'
export type EventNameType = 'message' | 'statusChanged'
export type MessageSubscriberType = (messages: ChatMessageType[]) => void;
export type StatusSubscriberType = (status: StatusType) => void;

export  type ChatMessageType = {
    message: string
    photo:   string
    userId: number
    userName: string
}
//массив подписчиков на сообщения и на изменения статуса ws
const subscribers = {
    'message': [] as MessageSubscriberType[],
    'statusChanged': [] as StatusSubscriberType[]
};

let newSocket: WebSocket | null = null;

//уведомление подписчиков о состоянии статуса ws
const notifySubscrAboutChangeStatus = (status:StatusType) =>{
    subscribers['statusChanged'].forEach(s=>s(status));
};

// попытки открытия канала WS каждые 3сек при отсутствии подключения к сети
const closeHandler = () =>{
    notifySubscrAboutChangeStatus('panding');
    console.log('closes ws');
    setTimeout(createChannel(),3000);
};

const messageHandler = (event:MessageEvent) =>{
    const newMessage =  JSON.parse(event.data);//получаем сообщения
    subscribers['message'].forEach(s => s(newMessage));// передаем подписчикам
};
const openHandler = () =>{
   notifySubscrAboutChangeStatus('ready');
};
const errorHandler = () =>{
    notifySubscrAboutChangeStatus('error');
    console.error('restart page - status ws ERROR')
};
const cleanFunction=() =>{
    newSocket?.removeEventListener('close', closeHandler);
    newSocket?.removeEventListener('message', messageHandler);
    newSocket?.removeEventListener('open', openHandler);
    newSocket?.removeEventListener('error', errorHandler);
    newSocket?.close();
};
function createChannel()  {
    cleanFunction();
    newSocket =  new WebSocket("wss://social-network.samuraijs.com/handlers/ChatHandler.ashx\n");
    notifySubscrAboutChangeStatus('panding');
    newSocket.addEventListener('message', messageHandler);
    newSocket.addEventListener('close', closeHandler);
    newSocket.addEventListener('open', openHandler);
    newSocket.addEventListener('error', errorHandler);
}

export const chatApi = {

    startWsChannel(){
        createChannel()
    },
    stopWsChannel(){
        subscribers['message']=[];
        subscribers['statusChanged']=[];
        cleanFunction();
    },

  subscribe(eventName:EventNameType, callback:MessageSubscriberType | StatusSubscriberType){
    // @ts-ignore
      subscribers[eventName].push(callback);
      return () => {
          // @ts-ignore
          subscribers[eventName] = subscribers[eventName].filter(s => s !== callback)
      }
  },
    unsubscribe(eventName:EventNameType, callback:MessageSubscriberType | StatusSubscriberType) {
        // @ts-ignore
        subscribers[eventName] = subscribers[eventName].filter(s => s !== callback)
    },
    sendMessages (messages:string) {
        newSocket?.send(messages)
    },

};

