import React, {useEffect, useRef, useState} from "react";
import { UserOutlined } from '@ant-design/icons';
import classes from "./chat.module.css";
import {Avatar} from "antd";
import {Form, Formik} from "formik";
import FormikControl from "../Formik/FormikControl";
import {useDispatch, useSelector} from "react-redux";
import {startMessagesListening, stopMessagesListening, sendMessages} from "../../redux/chatReducer";
import {AppStateType} from "../../redux/store";


  type ChatMessageType = {
    message: string
    photo:   string
    userId: number
    userName: string
}

export const Chat:React.FC = () =>{
    const status = useSelector((state:AppStateType)=> state.chat.status);
   const dispatch = useDispatch();

   useEffect(()=>{
       dispatch(startMessagesListening());
       return()=>{
           dispatch(stopMessagesListening())
       }
   }, []);

    return <div className={classes.chat_container}>
        {status === 'error' && <div>Some error occured, please refrech page </div>}

            <ChatMessage />
            < ChatForm />

    </div>
};


const ChatMessage:React.FC<{}> = () =>{
    const messages = useSelector((state:AppStateType)=> state.chat.messages);
    const messageAnchorRef = useRef<HTMLDivElement>(null);
    const [autoScrollActive, setAutoScrollActive] = useState(false);

    const scrollHandler = (e:React.UIEvent<HTMLDivElement, UIEvent>) => {
        let element = e.currentTarget;
        if(Math.abs((element.scrollHeight - element.scrollTop) - element.clientHeight) < 500){
            !autoScrollActive && setAutoScrollActive(true)
        }else {
            autoScrollActive && setAutoScrollActive(false)
        }
    };
    //auto scrolling if add new message
    useEffect(()=>{
        if(autoScrollActive){
            messageAnchorRef.current?.scrollIntoView({behavior: 'smooth'})
        }
    }, [messages]);

    return <div className={classes.block_message} onScroll={scrollHandler}>
        {messages.map((m, index) =>
            <Message key={m.id} msg={m}/>
        )}
        <div ref={messageAnchorRef}> </div>
    </div>
};


const Message:React.FC<{msg:ChatMessageType}> = React.memo(({msg}) =>{
    console.log('qqqq');
    return <div className={classes.chat_mess} >
        {msg.photo!==""
            ?<img src={msg.photo} alt=""/>
            :<Avatar className={classes.avatar_style} alt={'avatar'}  icon={<UserOutlined />} />
        }
        <span>{msg.userName}</span>
        <p>{msg.message}</p>
        <hr/>
    </div>
});


const ChatForm:React.FC<{}> = () =>{

    const status = useSelector((state:AppStateType)=> state.chat.status);
    const dispatch = useDispatch();

    const  initialValues= {
        message:''
    };
    type initialValuesType  = typeof initialValues;

    const onSubmit=( values:any, onSubmitProps:any) =>{
        dispatch(sendMessages(values.message));
        onSubmitProps.resetForm()
    };
    return <div className={classes.chat_form}>
        <Formik
            enableReinitialize
            initialValues={initialValues}
            onSubmit={onSubmit}>
            {
                formik=><Form className={classes.form__formSearch}>
                    <FormikControl control='textarea'  label='Message' name='message'/>
                    <button className={classes.form__button} disabled={status !== 'ready'} type='submit' >Отправить</button>
                </Form>
            }
        </Formik>
        <p>{status}</p>
    </div>
};

