import React from "react";
import {Chat} from "../../components/chat/Chat";
import {useSelector} from "react-redux";
import {selectedIsAuth} from "../../redux/authSelectors";

 const ChatPage:React.FC = () =>{
     const isAuth = useSelector(selectedIsAuth);


    return <div style={{ width: "100%", textAlign: "center"}}>
        {!isAuth
            ? <div style={{margin: "0 auto",border: "3px solid #007803", padding: "20px", color: "#007803", fontWeight: "bold"}}>Чат доступен только для зарегистрированных пользователей</div>
            :<Chat/>
        }
    </div>
};


 export default ChatPage;